.chat-room-container {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    overflow: hidden; 
}
.chatRoomContainer {
    height: 100vh;
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    border-left: 1px solid rgb(239, 243, 244);
    border-right: 1px solid rgb(239, 243, 244);
    overflow: hidden; 
}
.chatRoomFloatingHeader {
    color: rgb(15, 20, 25);
    background-color: white;
    top: 5px;
    position: absolute;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
}
.chatRoomHeaderLeft {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.chatRoomHeaderRight {
    width: 80%;
    margin-right: 15%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 400;
}

.chatRoomChatContainer {
    width: 100%;
    height: 100vh;
    margin: 55px 0px 50px 0px; /* top right bottom left */
    display: flex;
    flex-direction: column;
    overflow: scroll;

    /* Hiding scroll bar */
    overflow: scroll;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: content-box;
}

.chatRoomChatRow {
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 0px 10px 0px 10px;
    /* background-color: rgb(15, 20, 25); */
    display: flex;
    align-items: center;
}
.chatRoomBubbleLeft {
    margin-left: 5px; 
    margin-right: auto;
    color: rgb(15, 20, 25);
    background-color: white;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 15px 15px 15px 0px;
    width: auto;
    max-width: 70%;
    padding: 5px 10px 5px 10px;
    display: block;
}
.chatRoomBubbleRight {
    margin-left: auto; 
    margin-right: 5px;
    color: white;
    background-color: var(--themeLightGreen);
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 15px 15px 0px 15px;
    width: auto;
    max-width: 70%;
    padding: 5px 10px 5px 10px;
    display: block;
}
.chatRoomBubbleText {
    font-weight: 500;
}

.chatRoomBubbleTimestamp {
    display: flex;
    color: lightgray;
    justify-content: flex-end;
}


.chatRoomFloatingFooter {
    color: white;
    bottom: 5px;
    position: absolute;
    width: 100%;
    height: 40px;
    display: flex;
    flex-direction: row;
    /* background-color: red; */
}
.chatRoomFooterLeft {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    /* background-color: black; */
}
.chatRoomFooterRight {
    float: right;
    width: 50px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}